import React, { useContext } from "react";
import { Fade } from "react-reveal";
import { errorMessage } from "../../portfolio";
import Button from "../../components/button/Button";
import "./ErrorMessage.css";

export default function ErrorMessage() {
	return (
		<Fade bottom duration={1000} distance="40px">
			<div className="error-main" id="errorMessage">
				<div className="errorMessage-main">
					<div className="errorMessage-text-div">
						<div>
							<h1 className="errorMessage-text">
								{" "}
								{errorMessage.title}{" "}
							</h1>

							<p className="errorMessage-text-p subTitle">
								{errorMessage.subTitle}
							</p>

							<div className="button-errorMessage-div">
								<Button text="Homepage" newTab={true} href="/" />
							</div>
						</div>
					</div>

					<div className="errorMessage-image-div">
						<img alt="server error" src={require("../../assets/images/pageError.svg")} />
					</div>
				</div>
			</div>
		</Fade>
	);
}
